import { useRef, useState } from 'react'
import { Typography, Button, TextField } from '@mui/material'
import { Wrapper } from './MySongListsStyle'
import SimpleComfirmModal from '../../SimpleComfirmModal/SimpleComfirmModal'
import { createSongList } from '../../../api/songList'

const TitleBar = (props) => {
  const [showCreateModal, setShowCreateModal] = useState(false)
  const listNameRef = useRef()

  //Modal
  const modalClose = (event, reason) => {
    // if (reason === 'backdropClick') {
    //   setShowCreateSongList(false)
    // }
    setShowCreateModal(false)
  }
  //
  const createSongListHandler = async () => {
    try {
      const songListName = listNameRef.current.value
      const data = {
        name: songListName
      }
      const response = await createSongList(data)
      props.getUserSongList()
      setShowCreateModal(false)
    } catch (e) {
      setShowCreateModal(false)
    }
  }
  return (
    <Wrapper>
      <Typography className='title' variant='h6'>
        
      </Typography>
      <Button
        variant='contained'
        color='error'
        onClick={() => {
          setShowCreateModal(true)
        }}
      >
        
      </Button>
      {/*  */}
      <SimpleComfirmModal
        show={showCreateModal}
        close={modalClose}
        title={''}
        confirmText={''}
        cancelText={''}
        onConfirm={createSongListHandler}
        onCancel={modalClose}
      >
        {
          <TextField
            id='filled-multiline-static'
            label=''
            inputRef={listNameRef}
            rows={4}
            sx={{ width: '100%', mb: 2 }}
          ></TextField>
        }
      </SimpleComfirmModal>
    </Wrapper>
  )
}
export default TitleBar
