import { useEffect, useState, forwardRef } from 'react'
import { Grid, Box, Typography, TextField } from '@mui/material'

const SongInfo = forwardRef((props, ref) => {
  const song = props.song
  const [name, setName] = useState('') //
  const [intro, setIntro] = useState('') //
  const [lyric, setLyric] = useState('') //
  const { nameRef, introRef, lyricRef } = ref.current

  useEffect(() => {
    if (song) {
      if (song.name) setName(song.name)
      if (song.intro) setIntro(song.intro)
      if (song.lyric) setLyric(song.lyric)
    }
  }, [song])
  //data parent 
  return (
    <>
      <Typography
        variant='subtitle1'
        sx={{ fontWeight: 700, marginBottom: 2, color: 'text.primary' }}
      >
        
      </Typography>
      <TextField
        required
        id='outlined-required'
        label=''
        value={name}
        inputRef={nameRef}
        onChange={(e) => setName(e.target.value)}
        sx={{ width: '100%', marginBottom: 2 }}
      />
      <Grid container spacing={2}>
        {/*  */}
        <Grid item xs={12} sm={6}>
          <TextField
            id='filled-multiline-static'
            label=''
            multiline
            value={intro}
            inputRef={introRef}
            onChange={(e) => setIntro(e.target.value)}
            rows={4}
            sx={{ width: '100%' }}
          />
        </Grid>
        {/*  */}
        <Grid item xs={12} sm={6}>
          <TextField
            id='filled-multiline-static'
            label=''
            multiline
            value={lyric}
            inputRef={lyricRef}
            onChange={(e) => setLyric(e.target.value)}
            rows={4}
            sx={{ width: '100%' }}
          />
        </Grid>
      </Grid>
    </>
  )
})
export default SongInfo
